* {
   padding: 0;
   margin: 0;
   border: 0;
   font-family: Tahoma, sans-serif, serif;
   font-size: 18px;
   /* color: rgb(247, 47, 47); */
}

.menu {
   display: block;
   height: 50px;
   /* justify-content: space-between; */
   text-align: center;
}

.item {
   margin: 0 50px 10px 20px;
   color: rgb(0, 0, 0);
}

.item:hover {
   background-color: rgb(0, 163, 163);
   color: white;
}

.item_active {
   color: rgb(220, 150, 0);
}
